#myimg{
    background-color: rgb(0, 0, 0);
    border: 1px solid rgb(255, 255, 255);
    transform: rotateZ(3deg);
    box-shadow:10px 10px 20px rgb(0, 0, 0,0.3);
    animation-timing-function: ease-in-out;
    transition: 1s;
}
#myimg:hover{
    transform: rotateZ(5deg);
    transform: scale(1.06);
    animation-timing-function: ease-in-out;
    border-radius: 10px;
   
}

